import React from "react"
import FluidImage from "../FluidImage"
import { Link } from "gatsby"
import { StaticQuery, graphql } from "gatsby"
// import { blogURI } from "../../../globals"
const POST_QUERY = graphql`
    query GETPROJECT{
        wpgraphql {
            posts(first: 5, where: {categoryId: 3}) {
                nodes {
                    title
                    excerpt
                    uri
                    featuredImage {
                        altText
                        sourceUrl
                        imageFile {
                            childImageSharp {
                                fluid(maxHeight: 600, maxWidth: 1000, quality: 100, cropFocus: CENTER) {
                                    ...GatsbyImageSharpFluid_withWebp_tracedSVG
                                }
                            }
                        }
                    }
                    CustomPost {
                        acreage
                        address
                        bedroom
                        fieldGroupName
                        price
                        wc
                    }
                }
            }
        }
    }
`
const Project = () => {
    return (
        <div className="project-category">
            {/* <div className="container"> */}
            <div className="title-sidebar">
                <h3>Dự án BĐS mới</h3>
            </div>
            <ul>
                <StaticQuery
                    query={POST_QUERY}
                    render={(data) => {

                        if (data.wpgraphql.posts) {
                            const posts = data.wpgraphql.posts.nodes

                            return (
                                <>
                                    {
                                        posts &&
                                        posts.map((prop, index) => (

                                            <li className="item" key={index}>
                                                <div className="featured-image">
                                                    <Link to={`${prop.uri}`}>
                                                        <div className="ov1"></div>
                                                        <div className="ov2"></div>
                                                        <div className="ov3"></div>
                                                        <div className="ov4"></div>
                                                        <FluidImage image={prop.featuredImage} alt={prop.title} />
                                                    </Link>
                                                </div>
                                                <div className="title">
                                                    <Link to={`${prop.uri}`} title={prop.title}>
                                                        <h3>{prop.title}</h3>
                                                    </Link>
                                                </div>
                                                <div className="excerpt">
                                                    <div className="address">
                                                        <span><i className="fa fa-map-marker" aria-hidden="true"></i>{prop.CustomPost.address}</span>
                                                    </div>
                                                    <div className="price">
                                                        <span>Giá: ~ {prop.CustomPost.price}</span>
                                                    </div>
                                                    <div className="d-flex justify-content-between">
                                                        <div>{prop.CustomPost.bedroom}</div>
                                                        <div>{prop.CustomPost.wc}</div>
                                                        <div>{prop.CustomPost.acreage}</div>
                                                    </div>
                                                </div>

                                            </li>
                                        ))
                                    }
                                </>
                            )
                        }
                        return null
                    }}
                />
            </ul>
            {/* </div> */}
        </div>
    )
}

export default Project


